/*全文字体样式*/
body {
  font-family: "Helvetica Neue", Helvetica, Arial, "PingFang SC", "Hiragino Sans GB", "Heiti SC", "Microsoft YaHei", "WenQuanYi Micro Hei", sans-serif !important; }

body, html {
  line-height: 1;
  font-family: 'PingFang SC', 'STHeitiSC-Light', 'Helvetica-Light', arial, sans-serif, 'Droid Sans Fallback';
  user-select: none;
  -webkit-tap-highlight-color: transparent;
  background: #222;
  color: #fff; }

/*----------------------------------------------------------------------------------------*/
/*头部样式代码*/
.header {
  height: 1.27rem;
  line-height: 1.27rem;
  position: fixed;
  top: 0;
  background-color: #eb6d00;
  text-align: center;
  font-size: 0.48rem;
  color: #ffffff;
  -webkit-box-shadow: none;
  box-shadow: none;
  z-index: 99;
  left: 0;
  right: 0; }

/*底部样式代码开始*/
.footer {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 1.307rem;
  line-height: 1.307rem;
  overflow: hidden;
  background-color: #ffffff;
  border-top: 1px solid #ccc;
  z-index: 99; }
  .footer .item {
    width: 25%;
    display: inline-block;
    float: left;
    text-align: center;
    height: 100%;
    position: relative; }
    .footer .item.active {
      color: #eb6d00; }

/*底部样式代码结束*/
/*中间部分样式*/
.g-content {
  overflow-y: auto;
  top: 0;
  bottom: 1.307rem;
  position: absolute;
  left: 0;
  right: 0;
  /* 增加该属性，可以增加弹性*/
  -webkit-overflow-scrolling: touch;
  z-index: 1;
  width: 100%; }

.content {
  overflow-y: auto;
  top: 1.27rem;
  bottom: 0;
  position: absolute;
  left: 0;
  right: 0;
  /* 增加该属性，可以增加弹性*/
  -webkit-overflow-scrolling: touch;
  z-index: 1;
  width: 100%; }

[v-cloak] {
  display: none; }

/*触摸的全局样式*/
.touch-active {
  background-color: #357AE8; }

